import { welcomePage } from "./pages/welcomePage"
import { startPage } from "./pages/startPage"
import { gamePage } from "./pages/gamePage"
import { resultado } from "./pages/resultado"
import { juego } from "./pages/gamePage"

const routes = [
    {
        route: /\/welcome$/,
        handler: welcomePage
    },
    {
        route: /\/start$/,
        handler: startPage
    },
    {
        route: /\/game$/,
        handler: gamePage
    },
    {
        route: /\/resultado$/,
        handler: resultado
    }
]


export function router(container: Element) {
    if (location.pathname = "/ppt-tp") {

        goTo("/welcome");
    } else if (location.pathname != "/ppt-tp") {
        goTo("/welcome");

    }


    function handleRouter(path: string) {
        for (const route of routes) {
            if (route.route.test(path)) {
                const div = route.handler(goTo);
                if (container.firstElementChild) {
                    container.innerHTML = ""
                }
                container.appendChild(div)
            }


            if (route.route.test("/game") && path == "/game") {
                juego(goTo);
            }
        }
    }

    function goTo(path: string) {
        let link = path;

        history.pushState({}, "", link)
        handleRouter(path)
    }

}